@import url("https://use.typekit.net/rbk4msf.css");
@font-face {
    font-family: adobeMedium;
    src: url(./assets/Adobe-Clean/AdobeClean-Medium.otf);
}
@font-face {
    font-family: adobeRegular;
    src: url(./assets/Adobe-Clean/AdobeClean-Light.otf);
}
@font-face {
    font-family: graphikBlack;
    src: url(./assets/Graphik/GraphikBlack.otf);
}
@font-face {
    font-family: graphikLight;
    src: url(./assets/Graphik/GraphikLight.otf);
}
@font-face {
    font-family: gorditaMedium;
    src: url(./assets/GorditaMedium.otf);
}
@font-face {
    font-family: gorditaRegular;
    src: url(./assets/GorditaRegular.otf);
}
*, *:before, *:after {
    box-sizing: border-box;
}
ul, li, p, h5, h6 {
    font-family: gorditaRegular;
    color: #4A6ED9;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}

#list {
    text-align: left;
}

h5 {
    font-size: 1.5em;
    font-weight: bold;
}
h6 {
    font-size: 1.25em;
    font-weight: bold;
}
#half-width {
    width: 25vw;
    border-radius: 3px;
    border: 1px solid #eaeaea;
}
#full-width {
    width: 50vw;
    border-radius: 3px;
    border: 1px solid #eaeaea;
}
.config p {
    font-size: x-large;
}
.config {
    position: absolute;
}
.control-wrap {
    float: left;
}
.liljess {
    position: absolute;
    margin-left: -70px;
    width: 140px;
    bottom: 10%;
    left: 0;
}
.img-body {
    width: 250px;
}
.body {
    overflow: hidden;
    position: absolute;
    z-index: -1;
    top: -115px;
    left: -10%;
}
.body .eyebrows {
    height: 100%;
    position: absolute;
    background-color: black;
    width: 100%;
    transition: left 0.6s;
    left: 15%;
}
.body .eyebrows.right {
    left: 20%;
}
.body .eyebrow {
    z-index: 1;
    position: absolute;
    background-color: black;
    height: 20px;
    width: 35px;
    top: -140px;
}
.body .eyebrow.one {
    left: 20%;
}
.body .eyebrow.two {
    left: 35%;
}
.body .pupils {
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    transition: left 0.6s;
    left: 7%;
}
.body .pupils.right {
    left: 20%;
}
.body .pupil {
    background: black;
    border-radius: 50%;
    content: "";
    display: block;
    height: 5px;
    position: absolute;
    width: 5px;
    top: -100px;
}
.body .pupil.one {
    left: 30%;
}
.body .pupil.two {
    left: 45%;
}
.body .eyes {
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    transition: left 0.6s;
    left: 10%;
    top: 60px;
}
.body .eyes.right {
    left: 15%;
}
.body .eye {
    border-radius: 50%;
    display: block;
    position: absolute;
    background: white;
    height: 33px;
    width: 33px;
}
.body .eye.one {
    left: 25%;
}
.body .eye.two {
    left: 40%;
}
.mouth {
    position: relative;
    z-index: 1;
    top: 30px;
    left: 35%;
}
.img-mouth {
    width: 20px;
}
.ball-left {
    background-color: #ff63f0;
    border-radius: 50%;
    height: 45px;
    overflow: hidden;
    position: relative;
    width: 45px;
    left: 30px;
    margin-top: 30px;
}
.ball-right {
    background-color: #ff63f0;
    border-radius: 50%;
    height: 45px;
    overflow: hidden;
    position: relative;
    width: 45px;
    left: 150px;
    margin-top: -45px;
}
.ring {
    background: #ff96f5;
    border-radius: 50%;
    height: 40px;
    position: absolute;
    width: 40px;
}
.ring:after {
    background-color: ghostwhite;
    border-radius: 50%;
    content: "";
    display: block;
    height: 33%;
    margin-top: -36%;
    margin-left: -36%;
    position: absolute;
    width: 33%;
    top: 50%;
    left: 50%;
}
.ring.one {
    margin-left: -40px;
    height: 35px;
    width: 35px;
    top: 30%;
    left: 110%;
}
.scooter {
    position: absolute;
    top: 60%;
    left: 20%;
    background: #ff4aed;
}
.scooter.base {
    position: absolute;
    height: 25px;
    width: 130px;
    left: 10px;
    z-index: -2;
}
.scooter.top {
    position: absolute;
    height: 40px;
    width: 20px;
    top: -50px;
    left: 135px;
}
.scooter.handle {
    border-radius: 50%;
    height: 30px;
    position: absolute;
    width: 30px;
    left: 130px;
    top: -65px;
    z-index: 5;
}
.shadow {
    background: #3A271C;
    box-shadow: 5px 0 50px #3A271C;
    border-radius: 50%;
    height: 23.3333333333px;
    opacity: 0.25;
    position: absolute;
    width: 190px;
    z-index: -1;
    left: 10px;
    bottom: -8px;
}
.instructions p {
    color: #4A6ED9;
    position: fixed;
    bottom: 10px;
    width: 100%;
    text-align: center;
}
h1 {
    font-family: graphikBlack;
    display: block;
    text-align: center;
}
h1.home {
    line-height: 85%;
    color: #4A6ED9;
    font-size: 12em;
    margin-top: -20vw;
}
h1.about {
    font-weight: 600;
    line-height: 90%;
    color: #4A6ED9;
    font-size: 8em;
    text-align: center;
}
h1.contact {
    font-weight: 600;
    line-height: 90%;
    color: #4A6ED9;
    font-size: 3em;
    text-align: center;
}
h1 span span {
    position: relative;
    opacity: 0;
    animation: move-text 1s forwards;
}
@keyframes move-text {
    0% {
        bottom: 1em;
        opacity: 1;
    }
    50% {
        bottom: -0.5em;
    }
    100% {
        bottom: 0;
        opacity: 1;
    }
}
@keyframes move-buttons {
    0% {
        bottom: 2em;
        opacity: 0;
    }
    50% {
        bottom: -2em;
        opacity: 0.5;
    }
    100% {
        bottom: 0;
        opacity: 1;
    }
}
.home-btn {
    opacity: 0;
    position: relative;
    width: 220px;
    height: 60px;
    margin: 0em 0.3em 0.3em 0.3em;
    text-decoration: none;
    color: #4A6ED9;
    font-size: 2em;
    text-align: center;
    animation: move-buttons 1s forwards;
    font-family: gorditaRegular;
}
.home-btn.portfolio {
    animation-delay: 0.3s;
}
.home-btn.about {
    animation-delay: 0.4s;
}
.home-btn:hover {
    color: #ff4cec;
    text-decoration: none;
    font-weight: bold;
}
.row {
    align-items: center;
    text-align: center;
    padding: 10px 10px;
}
.row.portfolio {
    align-content: center;
    margin-bottom: -2em;
}
.row.pengandink {
    background-color: #FFFAFF;
}
.row.home-btns, .row.title {
    justify-content: center;
}
hr {
    border: none;
    border-top: 2px dashed #4A6ED9;
    color: #fff;
    background-color: #fff;
    height: 1px;
    width: 30%;
}
.portfolio-img {
    width: 100%;
}
.project-icons {
    position: relative;
    margin-left: auto;
    margin-right: auto;
}
.icon {
    width: 35px;
    height: auto;
    margin-right: 5px;
    filter: invert(48%) sepia(71%) saturate(4648%) hue-rotate(213deg) brightness(91%) contrast(85%);
}
.icon:hover, .icon:focus {
    filter: invert(40%) sepia(93%) saturate(1482%) hue-rotate(280deg) brightness(102%) contrast(101%);
}
.tags {
    display: inline-block;
    background-color: yellow;
    margin-right: 5px;
    color: black;
    border-radius: 5px;
    padding: 0 5px 0 5px;
    font-size: 14px;
}
h4 {
    color: #4A6ED9;
    font-family: gorditaMedium;
    font-size: 2em;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
h3 {
    font-family: basic-sans, sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 4em;
    color: #4A6ED9;
    margin-left: auto;
    margin-right: auto;
}
.tagsdiv {
    margin-left: auto;
    margin-right: auto;
    align-content: center;
}
.content-container.about, .content-container.portfolio {
    padding: 5% 15% 5% 15%;
}
.footer {
    font-family: gorditaRegular;
    color: #4A6ED9;
    text-align: center;
    margin-top: 10%;
}
.cropped {
    width: 100%;
    /* width of container */
    height: auto;
    /* height of container */
    object-fit: cover;
}
#heart {
    width: 20px;
}
#kessler-slides {
    width: 80%;
}

#portfolio-navbar {
    position: fixed;
    background-color: #FFFAFF;
}

.nav {
    width: 100vw;
    height: 10vh;
    top: -1vh;
    position: fixed;
    justify-content: flex-end;
    animation: move-nav 1s ease-out;
    font-family: gorditaRegular;
}
.nav.portfolio {
    justify-content: flex-start;
    left: 5vh;
}
.nav.home {
    right: 5vh;
}
.nav.about {
    right: 5vh;
}
@keyframes move-nav {
    0% {
        top: -10vh;
    }
    50% {
        top: 5vh;
    }
    100% {
        top: -1vh;
    }
}
.nav-btn {
    width: 30px;
    height: 30px;
    margin: 0 1vh 0 1vh;
    filter: invert(48%) sepia(71%) saturate(4648%) hue-rotate(213deg) brightness(91%) contrast(85%);
}
.nav-btn.back-about {
    transform: rotate(-180deg);
    margin: 5vh 1vh 0 1vh;
    width: 50px;
    height: 50px;
}
.nav-btn.about {
    filter: invert(48%) sepia(71%) saturate(4648%) hue-rotate(213deg) brightness(91%) contrast(85%);
}
.nav-btn.back-portfolio {
    filter: invert(48%) sepia(71%) saturate(4648%) hue-rotate(213deg) brightness(91%) contrast(85%);
    margin: 5vh 1vh 0 1vh;
    width: 50px;
    height: 50px;
}
.nav-btn:hover {
    filter: invert(40%) sepia(93%) saturate(1482%) hue-rotate(280deg) brightness(102%) contrast(101%);
}
.nav-btn:hover.back-about {
    transform: rotate(180deg);
    transition: all 0.5s ease-in-out;
}
.nav-btn:hover.back-portfolio {
    transform: rotate(360deg);
    transition: all 0.5s ease-in-out;
}
.footer-icon {
    width: 30px;
    height: 30px;
    margin: 0 1vh 0 1vh;
    filter: invert(48%) sepia(71%) saturate(4648%) hue-rotate(213deg) brightness(91%) contrast(85%);
}
.footer-icon:hover {
    filter: invert(40%) sepia(93%) saturate(1482%) hue-rotate(280deg) brightness(102%) contrast(101%);
}
a {
    color: #4A6ED9;
    text-decoration: underline;
}
a:hover {
    color: #ff4cec;
}
/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    p, label {
        font-size: 2em;
    }
    .content-container.about, .content-container.portfolio {
        padding: 20% 5% 20% 5%;
    }
    .footer-icon {
        width: 50px;
        height: auto;
        margin-right: 15px;
    }
    h5 {
        font-size: 3em;
    }
    h4 {
        font-size: 3.5em;
    }
    h1.contact {
        font-size: 4em;
    }
    .btn-primary {
        font-size: 2.5em;
        padding: 20px 50px;
    }
    .form-control {
        padding: 50px 30px;
        line-height: 140%;
        /* Styles */
    }
    .nav {
        background-color: #FFFAFF;
    }
    .nav.about {
        left: 1vh;
    }
    .nav.portfolio {
        right: 1vh;
    }
    .nav-btn {
        margin: 0 0.5vh 0 0.5vh;
    }
    .nav-btn.back-portfolio, .nav-btn.back-about {
        margin: 3vh 1vh 0 1vh;
        width: 100px;
        height: 100px;
    }
    .home-btn {
        font-size: 2.25em;
    }
    @keyframes move-nav-home {
        0% {
            top: -10vh;
        }
        50% {
            top: 15vh;
        }
        100% {
            top: 8vh;
        }
    }
    .liljess {
        bottom: 30%;
    }
    .instructions p {
        display: none;
    }
    .instructions:after {
        content: 'click anywhere.';
        font-family: gorditaRegular;
        bottom: 250px;
        color: #4A6ED9;
        position: fixed;
        width: 100%;
        text-align: center;
        font-size: 2.2em;
    }
    .home {
        font-size: 1.5em;
        margin-top: -10vh;
        padding-bottom: 50px;
    }
    #heart {
        width: 50px;
    }
    .tags {
        font-size: 2.5em;
    }
    .h3 {
        font-size: 3em;
    }
}
#contact-form {
    font-family: gorditaRegular;
}
.btn-primary {
    background-color: #4A6ED9;
}
input, label {
    color: #4A6ED9;
}
